import React, { Component } from 'react';
import $ from 'jquery';
import url from './audio/gbqq.mp3';
import testphoto from './photos/test.jpg';
import { Link } from 'react-router-dom';

class Main extends Component {
    state = {
        date: {},
        showButton: false,
    };

    componentDidMount() {
        this.print();
        setInterval(() => {
            this.time(2023, 10, 19);
        }, 1000);
        
        // var audio = document.getElementById("audio");
        // setTimeout(() => audio.play(), 10);
    }

    print = () => {
        const self = this;
        $.fn.autotype = function () {
            var _this = $(this);
            var str = _this.html();
            str = str.replace(/(\s){2,}/g, "$1");
            var index = 0;
            var timer = () => {
                var current = str.slice(index, index + 1);
                if (current === '<') {
                    index = str.indexOf('>', index) + 1;
                } else {
                    index++;
                }
                if (index < str.length) { 
                    _this.html(str.substring(0, index) + (index & 1 ? '_' : ''));
                } else {
                    _this.html(str);
                    self.setState({ showButton: true });
                    clearTimeout(timer);
                    return;
                }
                setTimeout(timer, 40);
            };
            setTimeout(timer, 2);
        };
        $("#autotype").autotype();
    };

    time = (year, month, day) => {
        var dateNow = new Date();
        var dateJNR = new Date(year, month - 1, day);
        var d = parseInt((dateNow - dateJNR - 72000000) / (24 * 3600 * 1000));
        var hour = parseInt(((dateNow - dateJNR - 72000000) / (3600 * 1000)) % 24);
        var minute = parseInt((dateNow - dateJNR - 72000000) / (1000 * 60) % 60);
        var second = parseInt((dateNow - dateJNR - 72000000) / 1000 % 60);
        this.setState({ date: { d: d, hour: hour, minute: minute, second: second } });
    };

    render() {
        const { date, showButton } = this.state;

        return (
            <div className="App animated bounceInLeft">
                <div className="date">
                    {date.d !== undefined ? (
                        <p>: 
                            <span className="date-text">{date.d}</span>  
                            <span className="date-text">{date.hour}</span>  
                            <span className="date-text">{date.minute}</span>  
                            <span className="date-text">{date.second}</span>  
                        </p>
                    ) : null}
                </div>
                <div id="autotype">
                    <p></p>
                    <h1 style={{ fontWeight: 900 }}></h1>
                    <p></p>
                    <p></p>
                    <p></p>
                    <p>Inner Harbor, </p>  
                    <p></p>
                    <p></p>
                    <p></p>
                    <div style={{ textAlign: 'right' }}>
                        <p>Hunter Huang</p>
                        <p>20241019</p>
                    </div>
                </div>
                {showButton && (
                    <Link to="/gallery">
                        <button 
                            className="animated bounceInLeft"
                            style={{
                                marginTop: '20px', 
                                padding: '12px 24px', 
                                fontSize: '18px', 
                                cursor: 'pointer',
                                border: 'none',
                                borderRadius: '8px',
                                backgroundColor: '#ff69b4',
                                color: '#fff',
                                fontWeight: 'bold',
                                boxShadow: '0 4px 8px rgba(0, 0, 0, 0.2)',
                                transition: 'background-color 0.3s ease',
                            }}
                            onMouseEnter={(e) => e.target.style.backgroundColor = '#ff85c1'}
                            onMouseLeave={(e) => e.target.style.backgroundColor = '#ff69b4'}
                        >
                            
                        </button>
                    </Link>
                )}
                {/* <audio id="audio" src={url}></audio> */}
            </div>
        );
    }
}

export default Main;
